body {
  color: var(--primary-text-color);
  font-style: normal;
  font-family: var(--main-font);
  background-color: #f4f4f4;
  margin: 0;
}

html {
  box-sizing: border-box;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, h5, h6, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 767px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1279px) {
  .container {
    max-width: 1280px;
  }
}

:root {
  --main-font: "Manrope", sans-serif;
  --secondary-font: "Roboto", sans-serif;
  --toggle-font: "Poppins", sans-serif;
  --light-bgc: #f4f4f4 --dark-bgc: #2e2e2e;
  --elements-color: #e5e5e5;
  --primary-text-color: #111321;
  --accent-color: #4b48db;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 767px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1279px) {
