  }
}

.filter-section__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .filter-section__container {
    justify-content: center;
  }
}

.dropdown__categories {
  display: inline-block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .dropdown__categories {
    display: none;
  }
}

.dropdown__others {
  display: none;
}

@media screen and (min-width: 768px) {
  .dropdown__others {
    display: inline-block;
    position: relative;
  }
}

.dropdown__others:hover .dropdown__content, .dropdown__categories:hover .dropdown__content {
  display: block;
}

.dropdown__btn {
  width: 112px;
  color: #4440f6;
  cursor: pointer;
  background: #fff;
  border: 1px solid #4440f6;
  border-radius: 20px;
  padding: 7.5px 37px 7.5px 13px;
}

@media screen and (min-width: 768px) {
  .dropdown__btn {
    width: 112px;
    margin-left: 14px;
    padding-left: 24.5px;
    padding-right: 48.5px;
  }
}

@media screen and (min-width: 1280px) {
  .dropdown__btn {
    margin-left: 16px;
  }
}

.dropdown__btn:hover, .dropdown__btn:focus {
  color: #fff;
  background-color: #4440f6;
}

.dropdown__btn-icon-arrow-down {
  width: 14px;
  height: 14px;
  fill: #a2a2a2;
  position: absolute;
  top: 50%;
  right: 13px;
  transform: translateY(-50%);
}

@media screen and (min-width: 768px) {
  .dropdown__btn-icon-arrow-down {
    right: 24.5px;
  }
}

.dropdown__btn:focus + .dropdown__btn-icon-arrow-down, .dropdown__btn:hover + .dropdown__btn-icon-arrow-down, .dropdown__btn-icon-arrow-up {
  display: none;
}

.dropdown__btn:focus ~ .dropdown__btn-icon-arrow-up, .dropdown__btn:hover ~ .dropdown__btn-icon-arrow-up {
  width: 14px;
  height: 14px;
  fill: #fff;
  display: block;
  position: absolute;
  top: 50%;
  right: 13px;
  transform: translateY(-50%);
}

@media screen and (min-width: 768px) {
  .dropdown__btn:focus ~ .dropdown__btn-icon-arrow-up, .dropdown__btn:hover ~ .dropdown__btn-icon-arrow-up {
    right: 24.5px;
  }
}

.dropdown__content {
  min-width: 148px;
  z-index: 1;
  background-color: #f8f8f8;
  padding-top: 14px;
  padding-left: 14px;
  padding-right: 14px;
